import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import MaskView from '@/components/MaskView'

Vue.use(Router)

export default new Router({
    routes: [{
            path: '/',
            name: 'MaskView',
            component: MaskView
        },
        {
            path: '/hello',
            name: 'HelloWorld',
            component: HelloWorld
        }
    ]
})


