<template>
    <div id="maskView">
      {{ message }}

      <input type="text" v-model="address" @keyup.enter="getPharm" placeholder="시/구 또는 동까지 입력"/>
      <button @click="getPharm">run</button>

      
    </div>

</template>

<script>
import axios from 'axios'

export default {
  name: 'maskView',
  data () {
    return {
      message: 'Hello Mask',
      defaultUrl: 'https://8oi9s0nnth.apigw.ntruss.com/corona19-masks/v1',
      addSearchUrl: 'http://api.vworld.kr/req/search?key=D617FCBA-F7E8-30B2-BD4B-D447043625B2',
      urlType: [
        '/stores/json',
        '/sales/json',
        '/storesByGeo/json',
        '/storesByAddr/json'
      ],
      remainType: [
        {key: 'plenty', value: '재고 상태[100개 이상(녹색)', color: 'green'},
        {key: 'some', value: '30개 이상 100개미만(노랑색)', color: 'yellow'},
        {key: 'few', value: '2개 이상 30개 미만(빨강색)', color: 'red'},
        {key: 'empty', value: '1개 이하(회색)', color: 'grey'},
        {key: 'break', value: '판매중지', color: 'black'}
      ],
      storeType: [
        {key: '01', value: '약국'},
        {key: '02', value: '우체국'},
        {key: '03', value: '농협'}
      ],
      address: '',
      vmap: null,
      homePosition: [126.9531143, 37.4828207],
      markerLayer: null
    }
  },
  methods: {
    getPharm () {
      let url = this.defaultUrl
      let param = {}
      if (this.address.length > 0) {
        param = '?address=' + this.address
        url += this.urlType[3] + param
      } else {
        
        let curPoint = ol.proj.transform(this.vmap.frameState_.focus, 'EPSG:3857', 'EPSG:4326')
        param = '?lat=' + curPoint[1] + '&lng=' + curPoint[0]
        url += this.urlType[2] + param
      }
      axios
        .get(url)
        .then(res => {
          this.getSearchedPharm(res.data)
        })
    },
    initMap () {
      vw.ol3.CameraPosition.center = [14129709.590359, 4512313.7639686]
      vw.ol3.CameraPosition.zoom = 16;
      vw.ol3.MapOptions = {
        container: 'vmap',
        mapMode: '2d-map',
        basemapType: vw.ol3.BasemapType.GRAPHIC,
        controlDensity: vw.ol3.DensityType.EMPTY,
        interactionDensity: vw.ol3.DensityType.BASIC,
        controlsAutoArrange: true,
        homePosition: vw.ol3.CameraPosition,
        initPosition: vw.ol3.CameraPosition
      }
      this.vmap = new vw.ol3.Map("vmap", vw.ol3.MapOptions);
      this.vmap.getView().setCenter(ol.proj.transform(this.homePosition, 'EPSG:4326', 'EPSG:3857'))

      this.addMarkerLayer()
    },
    addMarkerLayer () {
      if (this.markerLayer != null) {
        this.vmap.removeLayer(this.markerLayer)
        this.markerLayer = null
      } else {
        this.markerLayer = new vw.ol3.layer.Marker(this.vmap)
        this.vmap.addLayer(this.markerLayer)
      }
    },
    initEventHandler () {
      this.vmap.on('click', function (evt) {
        console.log('event handler', evt)
      })
    },
    getSearchedPharm (data) {
      if (this.markerLayer != null) {
        this.markerLayer.removeAllMarker()
      }
      let stores = data.stores
      stores.forEach(element => {
        let storeInfo = element
        let remainStat = storeInfo.remain_stat
        let remainColor = 'grey'
        this.remainType.forEach(type => {
          if (type.key === remainStat) {
            remainStat = type.value
            remainColor = type.color
          }
        })
        vw.ol3.markerOption = {
          x: storeInfo.lng,
          y: storeInfo.lat,
          epsg: 'EPSG:4326',
          title: storeInfo.name,
          contents: storeInfo.addr + ' / ' + remainStat + ' / 입고시간 ' + storeInfo.stock_at,
          iconUrl: 'http://map.vworld.kr/images/ol3/marker_blue.png',
          text: {
            offsetX: 0.5,
            offsetY: 2.0,
            font: '12px Calibri,sans-serif bold',
            fill: {color: remainColor},
            stroke: {color: '#fff', width: 2},
            text: storeInfo.name
          },
          attr: {'id': 'marker' + storeInfo.code, 'name': storeInfo.name}
        }
        this.markerLayer.addMarker(vw.ol3.markerOption)
      })
    },
    getPharmNearByMe (data) {

    }

  },
  created () {
    this.initMap()
    this.initEventHandler()
  }
}
</script>



